<template>
<div class="main-ui-box" v-on:mousemove='dealMove($event)'>
  <div id="scene" class="main-back">
    <div class="main-bg" :style="bgStyle" data-depth="0.2"></div>
  </div>
  <div class="main-front">
    <div class="main-ui-left">
      <div class="aboutme">
        <div class="avatar">
          <div class="imgbox">
            <img src="../assets/logo.png">
          </div>
          <div class="nickbox">
            王泽润
          </div>
        </div>
        <div class="intro">
        </div>
        <div class="link-box">
          <div class="status">好无聊</div>
          <div class="link">我也是</div>
        </div>
      </div>
    </div>
    <div class="main-ui-mid"></div>
    <div class="main-ui-right">
      <div class="index-box">
        <div class="index-head">
          {{timeLog.hour}}:{{timeLog.min}}:{{timeLog.sec}}
          <font-awesome-icon :icon="['fas','signal']"/>
        </div>
        <div class="index-col">
          <div class="index">
            <div v-for="(item ,i) in list" class="passagepad" @click="textopen(item)">
              <card Path="../assets/something.md"/>
            </div>
          </div> 
          <div class="index-side"></div>
          
          <!-- <my-markdown/> -->
        </div>
        
      </div>
    </div>
  </div>    
</div>
</template>

<script>
import Parallax from 'parallax'
import { constants } from 'fs';
import { setInterval, clearInterval } from 'timers';
import card from './card.vue'
export default{
  components:{
    'my-markdown1': () => import('../assets/something.md'),
    card
  },
  name: 'Main',
  data() {
    return {
      screenWidth: '',
      screenHeight: '',
      screenWidthMid: '',
      screenHeightMid: '',
      scene: '',
      parallaxInstance: '',
      bgStyle:{
        backgroundPositionX: '0px',
        backgroundPositionY: '0px'
      },
      timeLog:{
        hour: '',
        min: '',
        sec: ''
      },
      list:[
        '../assets/something.md',
        '../assets/something.md'
      ]
    }
  },
  methods:{
    dealMove: function(event){
      var posx = event.x
      var posy = event.y
    },
    timer: function(){
      return setInterval(
        ()=>{
          this.showTime()
        }, 1000
      )
    },
    showTime: function(){
      var dt = new Date()
      this.timeLog.hour = dt.getHours()
      this.timeLog.min = dt.getMinutes()
      if (this.timeLog.min < 10){
        this.timeLog.min = '0' + this.timeLog.min + ''
      }
      this.timeLog.sec = dt.getSeconds()
      if (this.timeLog.sec < 10){
        this.timeLog.sec = '0' + this.timeLog.sec + ''
      }
    },
    textopen: function(item){

    }
  },
  created(){
    this.timer()
  },
  mounted(){
    this.screenWidth = document.body.clientWidth
    this.screenHeight = document.body.clientHeight
    this.scene = document.getElementById('scene')
    this.parallaxInstance = new Parallax(scene)
    console.log(this.parallaxInstance)
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth
        this.screenHeight = document.body.clientHeight
      })()
    }
  },
  beforeDestroy(){
    clearInterval(this.timer)
  }
}
</script>

<style scoped>
div{
  margin: 0px;
  padding: 0px;
  position: absolute;
  text-align: left;
}

.main-bg{
  top: -5%!important ;
  left: -5%!important;
  width:110%;
  height: 110%;
  background: red;
  z-index: 1;
  background-image: url("../assets/bg_nier.jpg");
  background-repeat: repeat;
  background-size: 100%;
}

.main-back{
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1;
}

.main-front{
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}

.main-ui-box {
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}

.main-ui-left{
  width: 30%;
  height: 100%;
  left: 0px;
  top: 0px;
}

.main-ui-mid{
  width: 20%;
  height: 100%;
  left: 30%;
  top: 0px;
  background: blue;
}

.main-ui-right{
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0px;
  background: yellow;
}

.aboutme{
  height: 50%;
  width: 100%;
  top: 25%;
  left: 0px;
  position: relative;
  transform: perspective(2000px) rotateY(30deg);
  z-index: 2;
}

.avatar{
  top:0px;
  width:100%;
  height:60%;
  left: 0px;
  position: relative;
  background: rgba(0, 0, 0, 0.3);
}

.intro{
  top: 10px;
  width: 100%;
  height: 40%;
  position: relative;
  left: 0px;
  background: rgba(0, 0, 0, 0.4);
}

.link-box{
  top: 20px;
  width:100%;
  height:35%;
  position: relative;
  left: 0px;
}

.link{
  width:49%;
  height: 100%;
  position: relative;
  left: 0px;
  font-size: 60px;
  background: rgba(0, 0, 0, 0.5);
}

.status{
  width: 49%;
  left: 51%;
  height: 100%;
  font-size: 60px;
  background: rgba(0, 0, 0, 0.5);
}

.imgbox{
  top:0px;
  left:10%;
  height: 200px;
  width: 200px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
}

.nickbox{
  left:10%;
  position: relative;
  width:100%;
  font-size: 60px;
  color: white;
}

.index-box{
  width:101%;
  height:85%;
  margin-top:6%;
  position: relative;
  transform: perspective(3000px) rotateY(-30deg);
  z-index: 2;
}
.index-head{
  height:80px;
  width:100%;
  position: relative;
  background: rgba(0, 0, 0, 0.5);
  font-size: 60px;
  color:  white;
}
.index-col{
  height:calc(100% - 60px);
  position: relative;
  margin-top:20px;
  background: rgba(0, 0, 0, 0.5);
}
.index{
  left: 0%;
  width:80%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  font-size: 20px;
  box-shadow:5px 2px 6px #000;
  overflow-y: scroll
}
.index-side{
  left: 80%;
  height: 100%;
  width:20%;
}

.passagepad{
  width: 98%;
  height: 33%;
  position: relative;
  margin: 1%;
  font-size: 20px;
  color: white;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3);
}
</style>




